// Project Management API v1 - Single Project
// GET /api/v1/projects/[projectId] - Get project details
// PATCH /api/v1/projects/[projectId] - Update project
// DELETE /api/v1/projects/[projectId] - Delete project

import { NextRequest, NextResponse } from 'next/server'
import { supabaseAdmin } from '@/lib/supabase/server'

interface RouteParams {
  params: Promise<{ projectId: string }>
}

// GET - Get project details with tasks summary
export async function GET(request: NextRequest, { params }: RouteParams) {
  try {
    const { projectId } = await params
    
    if (!projectId) {
      return NextResponse.json(
        { success: false, error: 'Project ID is required' },
        { status: 400 }
      )
    }
    
    // Get project with members and task counts
    const { data: project, error } = await supabaseAdmin
      .from('Project')
      .select(`
        *,
        members: ProjectMember(
          id,
          userId,
          role,
          permissions,
          joinedAt,
          user: User(id, name, email, avatarUrl)
        ),
        tasks: Task(
          id,
          status,
          priority,
          dueDate
        ),
        milestones: Milestone(
          id,
          name,
          dueDate,
          status,
          progress
        )
      `)
      .eq('id', projectId)
      .single()
    
    if (error) {
      if (error.code === 'PGRST116') {
        return NextResponse.json(
          { success: false, error: 'Project not found' },
          { status: 404 }
        )
      }
      throw error
    }
    
    // Calculate task statistics
    const tasks = project.tasks || []
    const taskStats = {
      total: tasks.length,
      completed: tasks.filter((t: any) => t.status === 'completed').length,
      inProgress: tasks.filter((t: any) => t.status === 'in_progress').length,
      blocked: tasks.filter((t: any) => t.status === 'blocked').length,
      overdue: tasks.filter((t: any) => t.dueDate && new Date(t.dueDate) < new Date() && t.status !== 'completed').length,
    }
    
    // Calculate progress
    const progress = taskStats.total > 0 
      ? Math.round((taskStats.completed / taskStats.total) * 100)
      : 0
    
    return NextResponse.json({
      success: true,
      data: {
        ...project,
        tasks: undefined, // Remove full task list from response (use tasks endpoint)
        taskStats,
        progress,
      },
    })
  } catch (error: any) {
    console.error('GET /api/v1/projects/[projectId] error:', error)
    return NextResponse.json(
      { success: false, error: error.message || 'Failed to fetch project' },
      { status: 500 }
    )
  }
}

// PATCH - Update project
export async function PATCH(request: NextRequest, { params }: RouteParams) {
  try {
    const { projectId } = await params
    const body = await request.json()
    
    if (!projectId) {
      return NextResponse.json(
        { success: false, error: 'Project ID is required' },
        { status: 400 }
      )
    }
    
    // Extract allowed fields for update
    const allowedFields = [
      'name',
      'description',
      'status',
      'priority',
      'visibility',
      'startDate',
      'dueDate',
      'completedDate',
      'budgetHours',
      'budgetAmount',
      'currency',
      'trackedHours',
      'trackedAmount',
      'progress',
      'color',
      'icon',
      'tags',
      'contactId',
      'dealId',
      'aiEstimatedHours',
      'aiEstimatedDeliveryDate',
      'aiConfidenceScore',
    ]
    
    const updateData: Record<string, any> = {}
    for (const field of allowedFields) {
      if (body[field] !== undefined) {
        updateData[field] = body[field]
      }
    }
    
    if (Object.keys(updateData).length === 0) {
      return NextResponse.json(
        { success: false, error: 'No valid fields to update' },
        { status: 400 }
      )
    }
    
    // Add updatedAt timestamp
    updateData.updatedAt = new Date().toISOString()
    
    // Update project
    const { data: project, error } = await supabaseAdmin
      .from('Project')
      .update(updateData)
      .eq('id', projectId)
      .select()
      .single()
    
    if (error) {
      if (error.code === 'PGRST116') {
        return NextResponse.json(
          { success: false, error: 'Project not found' },
          { status: 404 }
        )
      }
      throw error
    }
    
    return NextResponse.json({
      success: true,
      data: project,
    })
  } catch (error: any) {
    console.error('PATCH /api/v1/projects/[projectId] error:', error)
    return NextResponse.json(
      { success: false, error: error.message || 'Failed to update project' },
      { status: 500 }
    )
  }
}

// DELETE - Delete project (soft delete by setting status to archived, or hard delete)
export async function DELETE(request: NextRequest, { params }: RouteParams) {
  try {
    const { projectId } = await params
    const { searchParams } = new URL(request.url)
    const hardDelete = searchParams.get('hard') === 'true'
    
    if (!projectId) {
      return NextResponse.json(
        { success: false, error: 'Project ID is required' },
        { status: 400 }
      )
    }
    
    if (hardDelete) {
      // Hard delete - remove all related data
      // Delete tasks first
      await supabaseAdmin
        .from('Task')
        .delete()
        .eq('projectId', projectId)
      
      // Delete project members
      await supabaseAdmin
        .from('ProjectMember')
        .delete()
        .eq('projectId', projectId)
      
      // Delete milestones
      await supabaseAdmin
        .from('Milestone')
        .delete()
        .eq('projectId', projectId)
      
      // Delete the project
      const { error } = await supabaseAdmin
        .from('Project')
        .delete()
        .eq('id', projectId)
      
      if (error) throw error
    } else {
      // Soft delete - archive the project
      const { error } = await supabaseAdmin
        .from('Project')
        .update({ 
          status: 'archived',
          updatedAt: new Date().toISOString(),
        })
        .eq('id', projectId)
      
      if (error) throw error
    }
    
    return NextResponse.json({
      success: true,
      message: hardDelete ? 'Project permanently deleted' : 'Project archived',
    })
  } catch (error: any) {
    console.error('DELETE /api/v1/projects/[projectId] error:', error)
    return NextResponse.json(
      { success: false, error: error.message || 'Failed to delete project' },
      { status: 500 }
    )
  }
}
